/* Modal Window Styles */
.modal-container {
  height:100vh;
  display: flex;
  align-items: center;
}
.modal-background.false {
  display: none;
}
.modal.true {
    width: 300px;
    height: 300px;
    position: absolute;
    right: 0;
    left: 0;
    color:#fff;
    display: flex;
    align-items: center;
    margin: 0 auto;
    z-index: 2;
}

.modal > div {
  position: absolute;
}

.modal-image-carousel {
  position: absolute;
  bottom:0;
  width: 50vw;
}

.modal-background.true {
    background: rgba(0, 0, 0, .7);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    bottom: 0;
    z-index: 1;
}

.modal-close-btn a {
  position: absolute;
  right: 15px;
  top: 15px;
  color:#fff;
  cursor: pointer;
  z-index: 1;
}

.modal-thumbnails.true {
    position: fixed;
    display: flex;
    bottom: 15px;
    left: 0;
    right: 0;
    z-index: 1;
    justify-content: center;
}

.thumb {
  margin: 0 3px;
  cursor: pointer;
  width: 130px;
  height: 120px;
  border: solid 1px transparent;
}

.thumb.current-thumb {
  border: solid 1px #fff;
}

